import ApiService from './api';

class ScheduleHoursService {
    // Rcuprer tous les crneaux horaires
    static async getHours() {
        return ApiService.request('/hours');
    }

    // Rcuprer un crneau horaire spcifique
    static async getHour(id) { // Chang de getHours  getHour
        return ApiService.request(`/hours/${id}`);
    }

    // Crer un nouveau crneau horaire
    static async createHour(hourData) {
        return ApiService.request('/hours', {
            method: 'POST',
            body: JSON.stringify(hourData),
        });
    }

    // Modifier un crneau horaire
    static async updateHour(id, hourData) { // Chang de updateHours  updateHour
        return ApiService.request(`/hours/${id}`, {
            method: 'PUT',
            body: JSON.stringify(hourData),
        });
    }

    // Supprimer un crneau horaire
    static async deleteHour(id) {
        return ApiService.request(`/hours/${id}`, {
            method: 'DELETE',
        });
    }
}

export default ScheduleHoursService;
